.about{
  z-index:10;
  background-color: var(--body-color);
}
.about_img{
  width: 200px;
  border-radius: .5rem;
  justify-self: center;
  align-self: center;
}

.about_description{
  text-align: center;
  margin-bottom:var(--m-1-5)
}

.about_buttons{
  justify-self: center;
}

@media screen and (min-width:568px) {
  .about_container{
    grid-template-columns: repeat(2 ,1fr);
  }
  .about_description{
    justify-content: center;
  }
}